
span.clip{display:inline-block;width:100%;line-height:30px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap
}
.music{position:fixed;width:100%;height:100%;font-size:14px;line-height:1.5;font-family:poppin,'PingFang SC',Tahoma,Arial,\5FAE\8F6F\96C5\9ED1,sans-serif;color:rgba(225,225,225,0.8)
}
.music .bg{position:absolute;top:0;left:0;width:100%;height:100%;background-repeat:no-repeat;background-size:cover;background-position:50%;filter:blur(20px);opacity:.6
}
.music .mask{position:absolute;top:0;left:0;width:100%;height:100%;background-color:#292a2b;background-color:rgba(0,0,0,0.35);z-index:2
}
.music .show-music{position:absolute;margin-left:5%;width:90%;padding:5%;color:#e1e1e1;z-index:3
}
.music .show-music .left{float:left;width:40%;height:425px;overflow-y:scroll;overflow-x:hidden
}
.music .show-music .left::-webkit-scrollbar{width:8px;height:8px;background-color:#202020;border-radius:4px
}
.music .show-music .left::-webkit-scrollbar-thumb{background-color:rgba(235,87,86,0.73);border-radius:4px
}
.music .show-music .left table{display:block;width:100%;height:100%
}
.music .show-music .left table td{height:40px;line-height:40px;text-align:left;cursor:pointer
}
.music .show-music .left table td:first-child{max-width:300px
}
.music .show-music .left table td.control{width:130px;padding-top:0;opacity:0
}
.music .show-music .left table td.control i{font-size:18px;line-height:30px;margin-right:5px
}
.music .show-music .left table td.control:hover{opacity:1
}
.music .show-music .left table .living{display:none;width:10px;height:10px;margin-right:10px;background-image:url(http://y.gtimg.cn/mediastyle/yqq/img/wave.gif?max_age=2592000&v=78979d47cc7dc55cab5d36b4c96168d5)
}
.music .show-music .left table tr.on td{color:white
}
.music .show-music .left table tr.on td.control{opacity:1
}
.music .show-music .left table tr.on .living{display:inline-block
}
.music .show-music .left table tr:hover td.control{opacity:1
}
.music .show-music .right{float:left;width:40%;height:425px;overflow-x:hidden;overflow-y:scroll
}
.music .show-music .right::-webkit-scrollbar{width:8px;height:8px;background-color:#202020;border-radius:4px
}
.music .show-music .right::-webkit-scrollbar-thumb{background-color:rgba(235,87,86,0.73);border-radius:4px
}
.music .show-music .right .l-box .show-img{position:relative;display:block;margin:auto;width:240px;height:240px;border-radius:100%;background-size:cover;background-image:url(http://music.163.com/style/mobile/img/share/disc-ip6.png)
}
.music .show-music .right .l-box .show-img img{display:inline-block;margin-top:47px;margin-left:47px;width:61%;height:61%;transition:all 1s ease-out
}
.music .show-music .right .l-box .show-img img.animate-img{animation:showImg 35s linear infinite
}
@keyframes showImg{
0%{transform:rotate3d(0, 0, 0, 360deg)
}
100%{transform:rotate3d(0, 0, 1, 360deg)
}
}
.music .show-music .right .l-box .l-lyr{transition:all 1s ease
}
.music .show-music .right .l-box .l-lyr p{text-align:center;line-height:35px;transition:all 0.5s ease-out
}
.music .show-music .right .l-box .l-lyr p.on{color:#31c27c
}
.music .show-music .controls{position:fixed;width:50%;margin-left:10%;bottom:10px;color:#e1e1e1
}
.music .show-music .controls .control-label{float:left;width:200px;height:90px
}
.music .show-music .controls .control-label i{font-size:40px;margin-right:10px;cursor:pointer
}
.music .show-music .controls .show-info{width:65%;margin-top:5px;float:left
}
.music .show-music .controls .show-info .i-l{float:left
}
.music .show-music .controls .show-info .i-r{float:right
}
.music .show-music .controls .progress{position:relative;width:65%;display:inline-block;margin-top:10px;padding-top:10px;height:20px;cursor:pointer
}
.music .show-music .controls .progress .line{position:relative;height:2px;background:rgba(255,255,255,0.2)
}
.music .show-music .controls .progress .lineTo{position:absolute;top:10px;left:0px;width:0%;height:2px;background:rgba(255,255,255,0.5);transition:all 0.1s linear;z-index:2
}
.music .show-music .controls .progress .lineIn{position:absolute;top:10px;left:0px;width:0%;height:2px;background:rgba(255,255,255,0.7);transition:all 0.1s linear;z-index:10
}
.music .show-music .controls .progress .lineIn i{position:absolute;font-size:28px;top:-20px;right:5px;width:10px;height:10px;background-position:0 -170px;opacity:1;filter:none;color:white
}
.music .show-music .controls .volume{float:right;position:relative;width:120px
}
.music .show-music .controls .volume i{cursor:pointer;font-size:25px
}
.music .show-music .controls .volume .volume-line{position:absolute;width:85px;top:20px;left:35px;height:4px;background:rgba(255,255,255,0.2);z-index:1;cursor:pointer
}
.music .show-music .controls .volume .volume-to{position:absolute;top:20px;left:35px;height:4px;background:rgba(255,255,255,0.8);z-index:2;cursor:pointer
}
.music .show-music .controls .volume .volume-to i{position:absolute;top:-16px;right:-8px;z-index:3
}
.music .show-music .serach-music input{width:140px;line-height:30px;border:0;font-size:18px;outline:inherit;z-index:1;padding-left:5px
}
.music .show-music .serach-music select{width:80px;height:31px;border:0;font-size:18px;outline:inherit;z-index:1;padding-left:5px
}
.music .show-music .serach-music button{width:50px;height:30px;font-size:16px;border:0;outline:none;cursor:pointer;background-color:white
}
@media screen and (max-width: 1400px){
.music .show-music{margin-left:2%;padding:4%
}
.music .show-music .left table td{width:70px;font-size:14px
}
.music .show-music .left table td:first-child{max-width:250px
}
.music .show-music .right{width:40%
}
.music .show-music .controls{margin-left:0;width:70%
}
}
@media screen and (max-width: 1000px){
.music .show-music .right{width:50%
}
}
@media screen and (max-width: 500px){
.music .show-music{width:100%;margin-left:0;padding-left:1%
}
.music .show-music .left{width:100%;height:300px
}
.music .show-music .left table td{line-height:30px;height:30px
}
.music .show-music .left table td:first-child{max-width:200px
}
.music .show-music .left table td.control i{font-size:16px;margin-right:5px
}
.music .show-music .right{width:100%;height:40px;text-align:left
}
.music .show-music .right .l-box{margin-left:-20%;margin-top:-230px
}
.music .show-music .controls{width:80%;margin-left:10%;bottom:10px
}
.music .show-music .controls .control-label{width:80%;height:60px;text-align:center
}
.music .show-music .controls .show-info{margin-top:-110px;width:80%
}
.music .show-music .controls .progress{width:80%;margin-top:-10px;margin-left:0
}
.music .show-music .controls .volume{float:left
}
}



